@use "global" as *;

.page-siteMap__nav {
  position: relative;
}

.page-siteMap__nav::before {
  content: "";
  background-image: url(../images/common/blueFish-decoration-left.svg);
  background-repeat: no-repeat;
  background-position: top;
  background-size: contain;
  position: absolute;
  top: rem(-12);
  left: calc(52% - min(48vw, rem(630)));
  max-width: rem(120);
  width: 100%;
  height: 100%;
  z-index: -1;
  @include mq("md") {
    top: rem(-72);
    max-width: rem(301);
  }
}
